<template>
  <div class="container">
    <h1>User Page</h1>
    <button class="btn btn-danger" @click="backToHome">Back to Home</button>
    <button class="btn btn-danger" @click="goToAdminProfile">Go to admin profile</button>
    <button class="btn btn-danger" @click="goToMemberProfile">Go to member profile</button>
    <router-view></router-view>
  </div>
</template>

<!--Ep16 | #09 | Theo di s thay i ca params trn routes Vue.js - RHP Team-->

<script>
  export default {
    name: "User",
    data () {
      return {
        sinhVien: ['thinh', 'lam'],
      }
    },
    methods: {
      backToHome: function () {
        this.$router.push('/');
      },
      goToAdminProfile: function () {
        this.$router.push('/user/admin')
      },
      goToMemberProfile: function () {
        this.$router.push('/user/memberX')
      }
    }
  }
</script>

<style scoped>

</style>



