<template>
  <div class="container">
    <h1>User Edit Page</h1>
    <h2>User Id: {{ id }}</h2>
    <p>Location: {{ location }}</p>
    <p>Browser: {{ brown }}</p>
    <button
      class="btn btn-primary"
      @click="show">Something</button>
    <div style="height: 500px"></div>
    <div id="thinhlam159" @click="toHashTag" style="height: 50px; background: cornflowerblue; cursor: pointer"><h3><a
      href="#thinhlam159">Thinh Lam 159</a></h3></div>
    <div style="height: 1500px"></div>
  </div>
</template>

<script>
  export default {
    name: "UserEdit",
    data() {
      return {
        id: this.$route.params.id,
        location: this.$route.query.location,
        brown: this.$route.query.browser,
        link: {name: 'UserEdit', params: {id: this.$route.params.id}, query: {location: 'vi', browser: 'chrome'}},
        status: false
      }
    },
    methods: {
      toHashTag: function () {
        this.$router.push({
          name: 'UserEdit',
          params: {id: this.$route.params.id},
          query: {location: 'vi', browser: 'chrome'}
        });
      },
      show () {
        this.status = true;
        console.log('bat che do huy diet')
      }
    },
    created: function () {
      console.log('created');
    },
    beforeRouteLeave:function (to, from, next) {
      if(this.status) {
        next()
      }else {
        if(confirm('Are u wanna leave? Any data will not save!')){
          next()
        }else {
          next(false);
        }
      }
    }
  }
</script>

<style scoped>
  a{
    text-decoration: none;
  }
</style>



