<template>
  <div class="container">
    <h1>Error 404</h1>
    <router-link
      tag="button"
      :to="{name : 'Home'}"
      class="btn btn-success">Back to home
    </router-link>
    <button class="btn btn-primary" @click="backToHome">Back to Home</button>
  </div>
</template>

<script>
  export default {
    name: "Error404",
    created() {
      console.log('created')
    },
    methods: {
      backToHome: function () {
        this.$router.push('/')
      }
    }
  }
</script>

<style scoped>

</style>



