<template>
  <div class="container">
    <p>this is product: {{ tenResult }}</p>
    <p>this is name product {{ nameResult }}</p>
    <hr>
    <p v-model="Info">Thong tin sv: {{ Info.id }}, {{ Info.ten }}, {{ Info.tuoi }}</p>
    <p>So keo: {{ getSokeo }} </p>
    <button @click="getNumberCandy">Get More Candy</button>
    <!--      <input type="text" v-model="Info" >-->
    <hr>
<!--    <input type="text" :value="value" @input="updateValue">-->
    <input type="text" v-model="value">
    <p>{{ value }}</p>
  </div>
</template>

<script>
  import {mapGetters, mapMutations, mapActions} from "vuex"

  export default {
    name: "Other",
    data() {
      return {
        // value:""
      }
    },
    methods: {
      // getnumbercandy() {
      //   this.getNumberCandy({amount: 10})
      // }
      getNumberCandy() {
        this.$store.commit("getNumberCandy", {amount: 10})
      },
      // updateValue: function (event) {
      //   this.$store.dispatch('updateValue', event.target.value2)
      // }
    },
    computed:
      {
        ...mapGetters([
          "tenResult", "nameResult",
        ]),
        Info() {
          return this.$store.getters.getSv(2)
        },
        getSokeo() {
          return this.$store.state.numberCandy;
        },
        // ...mapMutations(["getNumberCandy"]),
        // svInfo(){
        //   return this.$store.commit({type:'getSoKeo',id: 1})
        // // }
        // value2() {
        //   return this.$store.state.value;
        // },
        value: {
          get(){
            return this.$store.getters.getValue;
          },
          set(value) {
            this.$store.dispatch('updateValue', value)
          }
        }
      }
  }
</script>

<style scoped>

</style>



