<template>
  <div class="container">
    <div class="row">
    <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
      <h2>Email: </h2>
    </div>
  </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <div class="form-group">
          <label for="name">Name: </label>
          <input
            type="text"
            id="name"
            class="form-control"
            v-model="userInfo.name">
        </div>
        <div class="form-group">
          <label for="password">Password: </label>
          <input
            type="text"
            id="password"
            class="form-control"
            v-model="userInfo.password">
        </div>
        <div class="form-group">
          <label for="age">Age: </label>
          <input
            type="text"
            id="age"
            class="form-control"
            v-model="userInfo.age">
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="message">Message</label>
        <textarea
          id="message"
          placeholder="Plz hold text something..."
          class="form-control"
          rows="5"
          v-model="userInfo.message"></textarea>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <div class="form-group">
          <label for="sendmail">
            <input
              type="checkbox"
              id="sendmail"
              value="Send Email"
              v-model="userInfo.sendmail"> Send Mail
          </label>
          <label for="sendmail">
            <input
              type="checkbox"
              id="sendinfo"
              value="Send info"
              v-model="userInfo.sendmail"> Send Infomail
          </label>
          <label for="sendmail">
            <input
              type="checkbox"
              id="cutmail"
              value="Cut Mail"
              v-model="userInfo.sendmail"> Cut Mail
          </label>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="male">
          <input
            type="radio"
            id="male"
            value="Male"
            v-model="userInfo.gender"
            :checked="true">Male
        </label>
        <label for="female">
          <input
            type="radio"
            id="female"
            value="Female"
            v-model="userInfo.gender">Female
        </label>
      </div>
    </div>
    <!--    priority-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="priority">Priority</label>
        <select
          id="priority"
          class="form-control"
          v-model="userInfo.priority">
          <option disabled value="">Please select one</option>
          <option v-for="item in priority" v-bind:key="item">{{ item }}</option>
        </select>
      </div>
    </div>
    <!--    switch-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <app-switch-control v-model="userInfo.dataSwitch"></app-switch-control>
      </div>
    </div>
    <!--    button-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <button
          type="submit"
          class="btn btn-primary"
          @click.prevent="submited">Submit
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  import SwitchControl from "./SwitchControl";

  export default {
    name: "EditInfo",
    data() {
      return {
        userInfo: {
          name:'',
          password: '',
          age: '',
          message:'',
          sendmail: [],
          gender: '',
          priority: '',
          dataSwitch: true
        },
        priority: ['High', 'Medium', 'Low'],
        isSubmit: false,
      }
    },
    methods: {
      submited() {
        this.isSubmit= true;
      }
    },
    components: {
      appSwitchControl: SwitchControl
    }
  }
</script>

<style scoped>

</style>



