import Home from "./components/Home";
import User from "./components/user/User";
// import AppHello from "./components/Home/AppHello";
// import Contact from "./components/Contact/Contact";
import MiniProject from "./components/MiniProject/MiniProject";
import FilterAndMixins from "./components/FilterAndMixins/FilterAndMixins";
import Transitions from "./components/Transition and Animation/Transitions";
import Animations from "./components/Transition and Animation/Animations";
import Index from "./components/user/Index";
import UserDetails from "./components/user/UserDetails";
import UserEdit from "./components/user/UserEdit";
import Header from "./components/layout/Header";
import Error404 from "./components/error/Error404";
import LearnVueX from "./components/vuex/LearnVueX";
import VueResource from "./components/vueresource/VueResource";
import Form from "./components/Form/Form";
import LoginSignup from "./components/Form/LoginSignup";
import UserInfo from "./components/Form/UserInfo";
import EditInfo from "./components/Form/EditInfo";
import UserIndex from "./components/Form/UserIndex";

export const routes = [
  {
    path: '/', name: 'Home', components: {
      default: Home,
      "page-header": Header
    }
  },
  {
    path: '/user', name: 'User', component: User, children: [
      {path: '', name: 'Index', component: Index},
      {
        path: ':id', name: 'UserDetails', component: UserDetails, beforeEnter: (to, from, next) => {
          console.log('Action route guard');
          next()
        }
      },
      {path: ':id/edit', name: 'UserEdit', component: UserEdit},
    ]
  },
  {path: '/miniproject', name: 'MiniProject', component: MiniProject},
  {path: '/filterandmixins', name: 'FilterAndMixin', component: FilterAndMixins},
  {path: '/transitions', name: 'Transitions', component: Transitions},
  {path: '/animations', name: 'Animations', component: Animations},
  {path: '/learnvuex', name: 'LearnVueX', component: LearnVueX},
  {path: '/vueresource', name: 'VueResource', component: VueResource},
  {
    path: '/loginsignup', name: 'LoginSignup', component: LoginSignup, children: [
      {path: '', name: 'UserIndex', component: UserIndex, },
      {path: ':id', name: 'UserInfo', component: UserInfo, },
      {path: ':id/editinfo', name: 'EditInfo', component: EditInfo}
    ]
  },
  {path: '/form', name: 'Form', component: Form},
  {path: '/error404', name: 'Error404', component: Error404},
  {path: '/auth-redirect', redirect: {name: 'Home'}},
  {path: '/*', redirect: {name: 'Error404'}},
]



