import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    result: 0,
    svInfo: [
      { id: 1, ten: 'thinh', tuoi: 25, sokeo: 5 },
      { id: 2, ten: 'lam', tuoi: 24, sokeo: 10 }
    ],
    numberCandy: 0,
    value: "",
  },
  mutations: {
    increment(state) {
      state.result++
    },
    decrement(state) {
      state.result--
    },
    incrementOp(state, n) {
      state.result += n;
    },
    getNumberCandy(state, payload) {
      state.numberCandy -= payload.amount;
    },
    // getSoKeo: (state) => (id) => state.svInfo.find( a => a.id === id),
    updateValue(state, payload) {
      state.value = payload;
    }
  },
  getters: {
    tenResult(state) {
      return state.result * 10;
    },
    nameResult(state) {
      return state.result + " Name Product";
    },
    getSv(state) {
      return function (id) {
        return state.svInfo.find( a => a.id === id)
      }
    },
    getValue(state) {
      return  state.value;
    }
  },
  actions: {
    increment({ commit }) {
      commit('increment')
    },
    asyncDecrement({ commit }) {
      setTimeout(
        () => { commit('decrement')}, 500
      )
    },
    updateValue({ commit }, payload) {
      commit('updateValue', payload);
    }
  }
});



