<template>
  <div>
    <p>Gia tri: {{ result }}</p>
  </div>
</template>

<script>
  export default {
    name: "Result",
    computed: {
      result() {
        return this.$store.state.result;
      }
    },
    data() {
      return {
      }
    }
  }
</script>

