<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left"></div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="name">Name: </label>
        <input
          type="text"
          id="name"
          v-model="userInfo.name">
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="password">Password: </label>
        <input
          type="text"
          id="password"
          v-model="userInfo.password">
      </div>
    </div>
    <div class="row">
    <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
      <label for="age">Age: </label>
      <input
        type="text"
        id="age"
        v-model="userInfo.age">
    </div>
  </div>

    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="message">Message</label>
        <textarea
          id="message"
          placeholder="Plz hold text something..."
          class="form-control"
          rows="5"
          v-model="userInfo.message"></textarea>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <div class="form-group">
          <label for="sendmail">
            <input
              type="checkbox"
              id="sendmail"
              value="Send Email"
              v-model="userInfo.sendmail"> Send Mail
          </label>
          <label for="sendmail">
            <input
              type="checkbox"
              id="sendinfo"
              value="Send info"
              v-model="userInfo.sendmail"> Send Infomail
          </label>
          <label for="sendmail">
            <input
              type="checkbox"
              id="cutmail"
              value="Cut Mail"
              v-model="userInfo.sendmail"> Cut Mail
          </label>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="male">
          <input
            type="radio"
            id="male"
            value="Male"
            v-model="userInfo.gender"
            :checked="true">Male
        </label>
        <label for="female">
          <input
            type="radio"
            id="female"
            value="Female"
            v-model="userInfo.gender">Female
        </label>
      </div>
    </div>
<!--    priority-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <label for="priority">Priority</label>
        <select
          id="priority"
          class="form-control"
          v-model="userInfo.priority">
          <option disabled value="">Please select one</option>
          <option v-for="item in priority" v-bind:key="item">{{ item }}</option>
        </select>
      </div>
    </div>
<!--    switch-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <app-switch-control v-model="userInfo.dataSwitch"></app-switch-control>
      </div>
    </div>
<!--    button-->
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <button
          type="submit"
          class="btn btn-primary"
          @click.prevent="submited">Submit</button>
      </div>
    </div>
<!--    hien thi thong tin-->
    <div class="row" v-if="isSubmit">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <div class="panel panel-default">
          <div class="panel-heading">
            <h3>Your Data</h3>
          </div>
          <div class="panel-body">
            <p>Email: {{ userInfo.name }}</p>
            <p>Password: {{ userInfo.password}}</p>
            <p>Age: {{ userInfo.age}}</p>
            <p style="white-space: pre">Message: {{ userInfo.message }}</p>
            <p><strong>Send mail</strong></p>
            <ul>
              <li v-for="item in userInfo.sendmail" v-bind:key="item" :aria-selected="'Low'">{{ item }}</li>
            </ul>
            <p>Gender: {{ userInfo.gender }}</p>
            <p>Priority: {{userInfo.priority}}</p>
            <p>Switch: {{ userInfo.dataSwitch }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import SwitchControl from "./SwitchControl";
  export default {
    name: "Form",
    data() {
      return {
        userInfo: {
          name:'',
          password: '',
          age: '',
          message:'',
          sendmail: [],
          gender: '',
          priority: '',
          dataSwitch: true
        },
        priority: ['High', 'Medium', 'Low'],
        isSubmit: false,
      }
    },
    methods: {
      submited() {
        this.isSubmit= true;
      }
    },
    components: {
      appSwitchControl: SwitchControl
    }
  }
</script>

<style scoped>

</style>



