<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 text-left">
        <h2>Vue Resource</h2>
        <br>
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            v-model="user.username">
        </div>
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            v-model="user.email">
        </div>
        <br>
        <button class="btn btn-primary btn-block" @click="submit" type="submit">Submit</button>
        <hr>
        <button class="btn btn-success btn-block" @click="getAllData" type="submit">Get All Data</button>
        <ul class="list-group">
          <li class="list-group-item" v-for="user in users">{{ user.username }} - {{ user.email }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "VueResource",
    data() {
      return {
        user: {
          username: '',
          email:''
        },
        users:[],
        resource: {}
      }
    },
    methods: {
      submit() {
        // // console.log(this.user)
        // this.$http.post('',this.user)
        // .then(response => {console.log(response)},
        
        //   error => {console.log(error)}
        // )
        this.resource.save({}, this.user)
      },
      getAllData() {
        this.$http.get('data.json')
        .then(response =>{
          this.users = response.body;
          console.log(this.users);
        })
        // .then(data => {
        //   const arr = [];
        //   for(let key in data) {
        //     arr.push(data[key]);
        //   }
        //   this.users = arr;
        // })
      },
      created() {
        this.resource = this.$resource('data.json');
      }
    }
  }
</script>

<style scoped>

</style>



