export const productMixin = {
  data() {
    return {
      products: ['Iphone', 'Samsung', 'Nokia', 'Sony', 'Bphone'],
      filterProduct: '',
    }
  },
  computed: {
    filteredProducts() {
      return this.products.filter(element => element.match(this.filterProduct));
    }
  }
}



