<template>
  <div>
    <app-items v-for="(item, index) in itemsAaa" v-bind:data="item" v-bind:key="item"  @click.native="removeItem(index)">{{ item }}</app-items>
  </div>
</template>

<script>
  import Items from "./Items";

  export default {
    name: "ItemsLayout",
    props: ["itemsAaa"]
    ,
    components: {
      appItems: Items,
    },
    methods: {
      removeItem: function (index) {
        this.$emit('deleteItem', index)
      }
    }
  }
</script>

<style scoped>
  div {
    display: inline-flex;
  }
</style>



