<template>
  <div>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3">
        <h2>filter and mixins</h2>
<!--        1. filter, filter global-->
        <p>{{ text | toLowerCase }}</p>
      </div>
    </div>
    <hr>
    <div class="row">
      <div class="col-xs-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3">
        <h2>filter products</h2>
        <input type="text" class="form-control bm-md-3" v-model="filterProduct">
        <div class="card">
          <div class="card-body">
            <ul>
<!--              2-->
              <li v-for="product in filteredProducts" v-bind:key="product">{{ product }}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <hr>
      <app-mixins></app-mixins>
  </div>
</template>

<script>
  import Mixins from "./Mixins";
  import { productMixin } from "../../productMixin";

  export default {
    name: "FilterAndMixins",
    // 3. mixins, mixins global
    mixins: [productMixin],
    data () {
      return {
        text: 'Xin Chao 500 Anh Em',
      }
    },
    filters: {
      toLowerCase(text) {
        return text.toLowerCase();
      }
    },
    components: {
      appMixins: Mixins,
    }
  }
</script>

<style scoped>

</style>



