<template>
  <div class="container">
    <h1>Learn Vue X</h1>
    <br>
    <app-counter/>
    <br>
    <app-result/>
    <hr>
    <app-other/>
  </div>
</template>

<script>
  import Counter from "./Counter";
  import Result from "./Result";
  import Other from "./Other";

  export default {
    name: "LearnVueX",
    data() {
      return {
        value: 0,
      }
    },
    components: {
      appCounter: Counter,
      appResult: Result,
      appOther: Other
    }
  }
</script>

<style scoped>

</style>



