<template>
  <div class="row">
    <form action="">
      <div class="col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 form-group">
        <label for="">U can saying ...</label>
        <textarea name="" id="" cols="30" rows="10" class="form-group" v-model="item"></textarea>
      </div>
      <div class="col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3 form-group">
        <button class="btn btn-success" @click.prevent="createNew">Create Item</button>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    props: ['items'],
    name: "NewItem",
    data () {
      return {
        item: ''
      }
    },
    methods: {
      createNew: function () {
        if(this.item === ''){
          return false;
        } else if(this.items.includes(this.item)) {
          alert('this item is existed in card!')
        }
        else {
          this.$emit('itemAdded', this.item);
          this.item = '';
        }
      }
    }
  }
</script>

<style scoped>

</style>



