<template>
  <div id="app">
    <router-view name="page-header"/>
<!--    <appHeader></appHeader>-->
    <keep-alive>
      <transition name="slide" appear>
        <router-view></router-view>
      </transition>
    </keep-alive>
  </div>
</template>

<script>
  import Header from "./components/layout/Header";

  export default {
    name: 'App',
    data() {
      return {}
    },
    methods: {
      changeToday: function () {
        this.title = 'chu nhat';
      }
    },
    components: {
      appHeader: Header
    }
  }
</script>

<style>
  /*.slide-leave-active {*/
  /*  opacity: 0;*/
  /*  transition: opacity 1s ease;*/
  /*  animation: slide-out 1s ease-out forwards;*/
  /*}*/
  /*.slide-leave {*/
  /*}*/
  .slide-enter {
    opacity: 0;
  }

  .slide-enter-active {
    transition: opacity 1s ease;
    animation: slide-in 1s;
  }

  /*@keyframes slide-out {*/
  /*  from {*/
  /*    transform: translateX(0);*/
  /*  }*/
  /*  to {*/
  /*    transform: translateX(30px);*/
  /*  }*/
  /*}*/
  @keyframes slide-in {
    from {
      transform: translateX(-30px);
    }
    to {
      transform: translateX(0px);
    }
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 1000px;
    height: auto;
    margin: auto;

  }

  .subComponent {
    display: inline-flex;
  }
</style>



