<template>
  <div class="container">
    <p> Xin chao: {{ id }}</p>
    <router-link
      tag="button"
      class="btn btn-danger"
      :to="link">
      Edit Profile
    </router-link>
  </div>
</template>

<script>
  export default {
    name: "UserDetails",
    data() {
      return {
        id: this.$route.params.id,
        link: {
          name: 'UserEdit',
          params: {
            id: this.$route.params.id
          },
          query: {
            location: 'vi',
            browser: 'chrome'
          },
        }
      }
    },
    methods: {
      goToEditProfile: function () {
        this.$router.push('/user/' + this.id + '/edit')
      }
    },
    // mounted() {
    //   console.log(this.id);
    // },
    // beforeCreate() {
    //   console.log('beforecreate')
    // },
    // created() {
    //   console.log('created')
    // },
    watch: {
      '$route' (to, form) {
        this.id = to.params.id;
      }
    }
  }
</script>

<style scoped>

</style>



