<template>
  <div class="progress">
    <div class="progress-bar bg-warning" role="progressbar" :style="{width: (itemsCount / maxItem) * 100 + '%'}" aria-valuenow="75" aria-valuemin="0"
         aria-valuemax="100"></div>
    <p> {{ itemsCount }} / {{ maxItem }}</p>
  </div>
</template>

<script>

  export default {
    name: "Header",
    props: ['itemsCount', 'maxItem']
  }
</script>

<style scoped>

</style>



