<template>
  <div class="container">
<!--    <p>getter x 10: {{ x10Resule }}</p>-->
    <button class="btn btn-primary" @click="increment">Increment</button>
    <button class="btn btn-danger" @click="decrement">Decrement</button>
    <button class="btn btn-danger" @click="asyncDecrement">AsyncDecrement</button>
<!--    <button class="btn btn-warning" @click="x10Resule">Getter x 10</button>-->
  </div>
</template>

<script>
  import { mapMutations, mapActions } from 'vuex';
  export default {
    name: "Counter",
    props: [],
    methods: {
      ...mapMutations([ 'decrement']),
      ...mapActions(['increment', 'asyncDecrement'])
    },
    computed: {
      // x10Resule() {
      //   return this.$store.getters.tenResult;
      // }

    }
  }
</script>

<style scoped>

