<template>
  <div class="center">
    <div class="btns">
      <a class="a1" :class="{active: value}" href="#" @click="changeForm(true)">Sign up</a>
      <a class="a2" :class="{active: !value}"href="#" @click="changeForm(false)">Login</a>
    </div>
    <!--    singup-form-->
    <div class="signup-form" v-if="value">
      <div class="header">Signup For Free</div>
      <form action="/somethinh" @submit="checkForm">
        <p v-if="errors.length">
          <b style="color: #ffffff">Please correct the following error(s):</b>
          <ul>
            <li style="color: #ffffff" v-for="error in errors">{{ error }}</li>
          </ul>
        </p>
        <input type="text" placeholder="FirstName" v-model="users.firstname">
        <input class="lstname" type="text" placeholder="LastName" v-model="users.lastname">
        <input class="email" type="text" placeholder="Email or Phone" v-model="users.email">
        <input class="email" type="password" placeholder="Password" v-model="users.password">
        <input type="submit" value="Sign Up">
      </form>
    </div>
    <!--    login-form-->
    <div class="login-form" v-else @submit="checkForm">
      <div class="header hearder1">Login For Free</div>
      <form action="">
        <input class="email" id="email" name="email" type="email" placeholder="Email or Phone">
        <input class="email" type="password" placeholder="Password">
        <input type="submit" value="Login">
      </form>
    </div>
  </div>
</template>

<script>
  export default {
    name: "UserIndex",
    data() {
      return {
        value: true,
        errors: [],
        users: {
          firstname: '',
          lastname: '',
          email: '',
          password: '',
        }
      }
    },
    methods: {
      changeForm(value) {
        this.value = value;
      },
      checkForm: function (e) {
        if (this.users.firstname && this.users.lastname && this.users.email && this.users.password) {
          return true;
        }

        this.errors = [];

        if (!this.users.firstname) {
          this.errors.push('First Name required.');
        }
        if (!this.users.lastname) {
          this.errors.push('Last Name required.');
        }
        if (!this.users.email) {
          this.errors.push('Email required.');
        } else if (!this.validEmail(this.users.email)) {
          this.errors.push('Valid email required.');
        }
        if (!this.users.password) {
          this.errors.push('Password required.');
        }
        e.preventDefault();
      }
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  }
</script>

<style scoped>
  @import url('https://fonts.googleapis.com/css2?family=Crimson+Pro&family=Literata');

  body {
    margin: 0;
    padding: 0;
    background: linear-gradient(#bedef4, #93c8ec);
    height: 100vh;
    overflow: hidden;
    font-family: 'Crimson Pro', serif;
  }

  .center {
    margin: 70px auto;
    width: 540px;
    border: 1px solid #243342;
    border-radius: 5px;
    position: relative;
    background: #2c3e50;
  }

  .center .btns {
    display: flex;
    padding: 40px 0px 20px 30px;
  }

  .btns a {
    text-decoration: none;
    padding: 15px 75px;
    font-size: 30px;
    color: white;
    background: #34495e;
    border-radius: 2px;
  }

  .center .header {
    text-align: center;
    font-size: 35px;
    color: white;
    padding: 5px 0 30px 0;
  }

  form input {
    height: 45px;
    width: 210px;
    font-size: 20px;
    background: none;
    border: 1px solid #3498db;
    border-radius: 2px;
    padding: 0 10px;
    margin-left: 25px;
    margin-right: 25px;
    outline: none;
    color: white;
  }

  ::placeholder {
    color: #7dbde8;
  }

  form .lstname {

  }

  form .email {
    width: 475px;
    margin-top: 20px;
  }

  form input[type= 'submit'] {
    margin: 20px 0;
    width: 475px;
    height: 50px;
    font-size: 23px;
    cursor: pointer;
    color: white;
    background: #2981bc;
    border: 1px solid #2573a7;
    border-radius: 2px;
  }
  form input[type= 'submit']:hover {
    background: #2990d1;
    transition: .5s;
  }
  a:hover, a.active {
    background: #2981bc;
  }
</style>



