<template>
  <div class="container">
    <h2>Day la mini project</h2>
    <app-progress-bar :items-count="items.length" :max-item="maxItems"></app-progress-bar>
    <items-layout :items-aaa="items" @deleteItem="removeItem"></items-layout>
    <app-new-item @itemAdded="createNewItem" :items="items"></app-new-item>
  </div>
</template>

<script>
  import Progressbar from "./ProgressBar";
  import ItemsLayout from "./ItemsLayout";
  import NewItem from "./NewItem";

  export default {
    name: "MiniProject",
    data () {
      return {
        items: [
          'Nothing to say',
          'Now, i wanna say i miss u'
        ],
        maxItems: 10
      }
    },
    components: {
      itemsLayout: ItemsLayout,
      appNewItem: NewItem,
      appProgressBar: Progressbar
    },
    methods: {
      createNewItem: function (item) {
        this.items.push(item);
      },
      removeItem: function (index) {
        this.items.splice(index, 1);
      }
    }
  }
</script>

<style scoped>

</style>



